
@-webkit-keyframes rotateBottomToFront {
    0% {
      -webkit-transform: scale(1) rotateX(-90deg);
              transform: scale(1) rotateX(-90deg);
    }
    20% {
      -webkit-transform: scale(0.6) rotateX(-90deg);
              transform: scale(0.6) rotateX(-90deg);
    }
    90% {
      -webkit-transform: scale(0.6) rotateX(0deg);
              transform: scale(0.6) rotateX(0deg);
    }
    100% {
      -webkit-transform: scale(1) rotateX(0deg);
              transform: scale(1) rotateX(0deg);
    }
}
@keyframes rotateBottomToFront {
    0% {
      -webkit-transform: scale(1) rotateX(-90deg);
              transform: scale(1) rotateX(-90deg);
    }
    20% {
      -webkit-transform: scale(0.6) rotateX(-90deg);
              transform: scale(0.6) rotateX(-90deg);
    }
    90% {
      -webkit-transform: scale(0.6) rotateX(0deg);
              transform: scale(0.6) rotateX(0deg);
    }
    100% {
      -webkit-transform: scale(1) rotateX(0deg);
              transform: scale(1) rotateX(0deg);
    }
}



