.blink {
  -webkit-animation: blinkFrames 1s ease-in-out infinite;
          animation: blinkFrames 1s ease-in-out infinite;
}

@-webkit-keyframes blinkFrames {
    from {
      -webkit-transform: opacity(0);
              transform: opacity(0);
    }
    to {
      -webkit-transform: opacity(1);
              transform: opacity(1);
    }
}

@keyframes blinkFrames {
    from {
      -webkit-transform: opacity(0);
              transform: opacity(0);
    }
    to {
      -webkit-transform: opacity(1);
              transform: opacity(1);
    }
}



